@import '../../components/swiper.scss';
@import '../../components/partnerList.scss';
@import '../../components/contactArea.scss';
@import '../../components/floatButton.scss';

.product {
  &__banner {
    height: 600px;

    &-button {
      margin-top: 52px;
      background: #F7931E;
      color: #FFFFFF;

      &:hover {
        background: #fe9c2a;
        color: #FFFFFF;
      }

      &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: #F7931E, $alpha: .25)
      }
    }
  }

  &__title {
    position: relative;
    max-width: 530px;
    margin: auto;
    padding: 100px 0 128px;
    font-size:36px;
    font-weight:bold;
    color:#333333;
    text-align: center;

    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 100px;
      transform: translateX(-50%);
      display: inline-block;
      width:100px;
      height:8px;
      background:#2661E9;
    }
  }

  &__products {
    padding-bottom: 100px;
    background: #FFFFFF;
    overflow: hidden;

    &-content {
      position: relative;
      display: flex;
      align-items: center;
      margin-top: -20px;
    }

    &-col {
      padding: 0 7px;
    }

    &-list {
      padding: 0 30px 0 0;
    }

    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 14px;
      padding: 19.5px 15px;
      background: #F5F5F5;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        background: #2661E9;

        .product__products {
          &-title {
            color: #FFFFFF;
          }
        }
      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      object-fit: contain;
    }

    &-title {
      margin-left: 20px;
      margin-bottom: 0;
      font-size:14px;
      font-weight:bold;
      color:#333333;
    }

    &-main {
      position: relative;
      padding: 48px 0 76px 35px;
    }

    &-bg {
      position: absolute;
      top: 0;
      left: 90px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 0% 50%/auto 100%;
      transform: rotate(180deg);
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      &.hidden {
        display: none;
      }
    }
  }

  &__basic {
    padding-bottom: 80px;
    overflow: hidden;

    &-main {
      margin-top: -53px;
      padding: 53px 90px 70px 34px;
    }

    &-bg {
      position: absolute;
      top: 0;
      left: -90px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-2.png) no-repeat 50% 0/100%;
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      &.hidden {
        display: none;
      }
    }

    &-list {
      // padding: 0 35px;
      height: 500px;
    }

    &-item {
      display: flex;
      align-items: center;
      height: 75px;
      overflow: hidden;
      padding: 0 20px;
      background: #FFFFFF;
      color: #333333;
      cursor: pointer;
      transition: all .5s;

      .hover {
        display: none;
      }

      &.hover,
      &:hover {
        align-items: flex-start;
        height: auto;
        padding-top: 20px;
        padding-bottom: 20px;
        background: #2661E9;
        color: #FFFFFF;

        .hover {
          display: block;
        }

        .product__basic {
          &-top {
            display: none;
          }

          &-index {
            color: #FFFFFF;
          }
        }
      }

      & + & {
        margin-top: 4px;
      }
    }

    &-top {
      display: flex;
      align-items: center;
    }
  
    &-index {
      margin-right: 20px;
      font-size:40px;
      font-weight:bold;
      color:#2661E9;
      line-height: 1;
      transition: all .5s;
    }

    &-content {}

    &-title {
      margin: 5px 0;
      font-weight: bold;
    }

    &-desc {
      margin-top: 25px;
      margin-bottom: 0;
      font-size: 14px;
      color: #BDD0FA;
    }
  }

  &__feature {
    background: #FFFFFF;
    padding-bottom: 100px;
    overflow: hidden;

    &-main {
      position: relative;
      margin-top: -45px;
      padding: 45px 50px 78px 70px;
    }

    &-bg {
      position: absolute;
      top: 0;
      right: -20px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 50% 0%/100%;
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      &.hidden {
        display: none;
      }
    }

    &-list {
      // padding: 0 35px;
    }

    &-item {
      display: flex;
      align-items: center;
      padding: 15px 30px;
      background: #F5F5F5;
      font-weight:bold;
      cursor: pointer;
      transition: all .5s;
      
      &.hover,
      &:hover {
        background: #2661E9;
        
        .product__feature {
          &-num,
          &-title {
            color: #FFFFFF;
          }
        }
      }

      & + & {
        margin-top: 12px;
      }
    }

    &-num,
    &-title {
      transition: all .5s;
    }

    &-num {
      color: #2661E9;
      font-size:40px;
    }

    &-title {
      margin-left: 30px;
      margin-bottom: 0;
      color: #333333;
      font-size:14px;
    }
  }

  &__case {}

  &__service {
    background: #FFFFFF;
    padding-bottom: 100px;

    &-main {
      justify-content: center;
    }

    &-item {
      text-align: center;
      padding: 20px;
    }

    &-icon {
      width: 36px;
      height: 36px;
      object-fit: contain;
    }

    &-title {
      margin: 30px 0;
      font-weight: bold;
      color: #333333;
      line-height: 1;
    }

    &-desc {
      margin-bottom: 0;
    }
  }

  &__price {
    background: #ECECEC;
    padding-bottom: 100px;

    &-main {
      padding: 0 50px 50px;
      background: #FFFFFF;
    }
    
    &-table {
      font-size: 14px;
    }

    &-grey {
      background: #f9f9f9;
      border: none !important;

      th {
        border: none !important;
      }
    }

    &-head {
      font-size: 16px;
      color: #000000;
    }

    &-num {
      color: #F7931E;
      font-weight: bold;
    }

    &-icon {
      width: 26px;
      height: 26px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-size: 24px;
      background: #F7931E;
      border-radius: 50%;
      color: #FFFFFF
    }
  }

  &__partner {
    padding-bottom: 100px;
  }
}


@media (max-width: 1199px) {
  .product__products-bg,
  .product__basic-bg,
  .product__feature-bg {
    display: none;
  }
}

@media (max-width: 767px) {
  .product {
    &__price {
      &-main {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}